import React, {Component} from "react";
import PropTypes from 'prop-types';
import {integer, fraction} from "../../../../../magnum2/components/Utils";

const CheckoutPrice = (props) => {

        let checkoutPrice = props.checkoutPrice;
        if (!checkoutPrice) {
            return (<span className="h4 u-no-spacing">—</span>);
        }
        return (
            <div>
                <span className="u-acc-hide">Opłaty jednorazowe</span>
                <span className="h2 u-no-spacing">{integer(checkoutPrice.gross)}</span>
                <span className="h4 u-no-spacing">,{fraction(checkoutPrice.gross)}&nbsp;{checkoutPrice.currency}</span>
            </div>
        );

};

export default CheckoutPrice;

